                    <p>Comprar una vez</p>
                </div>

                
                <div class="B2B2_F8">
                    
                    <div class="B2B2_F8F">
                        <div v-on:click="selectM='Sus'" class="B2B2_FF"><div class="B2B2_F" :class="{B2B2_FRed: selectM=='Sus'}"></div></div>
                        <p class="B2B2_F8B2">suscripción</p>
                        <img class="B2B2_F8B3">
                    </div>
                    
                    <div v-if="selectM=='Sus'" class="B2B2_F8F2">
                        <div class="BLine"></div>
                        <div class="B2B2_F8F3" v-on:click="openWeeks= !openWeeks">
                            <div class="B2B2_F8C">
                                <p class="B2B2_F8T1">Frecuencia</p>
                                <div class="B2B2_F8T2"><p>{{selectWeek}}</p><img src="../assets/product/RowDown.svg"></div>
                            </div>
                            <div v-if="openWeeks">
                                <div v-on:click="selectWeek=item" v-for="(item, key) in weeks" :key="key" class="B2B2_F8T"><p>{{item}}</p></div>
                            </div>
                            
                        </div>
                        <p class="B2B2_F8F4">Obtendrás 5% de descuento en cada orden recurrente</p>
                    </div>

                </div>

                <p class="B2B2_T5">Cantidad</p>
                <div class="B2B2_F9">
                    <img class="showMV" v-on:click="selectNumb>1 ? selectNumb=selectNumb - 1 : selectNumb" src="../assets/product/RestRed.svg">
                    <img class="showDK" v-on:click="selectNumb>1 ? selectNumb=selectNumb - 1 : selectNumb" src="../assets/product/RestBlack.svg">
                    <p>{{selectNumb}}</p>
                    <img class="showMV" v-on:click="selectNumb=selectNumb + 1" src="../assets/product/PlusRed.svg">
                    <img class="showDK" v-on:click="selectNumb=selectNumb + 1" src="../assets/product/PlusBlack.svg">
                </div>

                <div class="B2B2_F10">
                    <div class="B2B2_F10B1"  @click="agregarItem(selectNumb)"><p>AGREGAR</p><img src="../assets/catalogo/Car.svg"></div>
                    <div class="B2B2_F10B2" 
                    @mouseover="hover1 = true"
                  @mouseleave="hover1 = false"
                  @click="agregarItem2(selectNumb)"><p>PAGAR</p></div>
                </div>

            </div>

        </div>

        <div class="B3">
            <p class="B3T1">Detalles del producto:</p>
            <div class="B3B">
                <div class="B3B1">
