                        <p class="showMV">Chiapas</p>
                        <p class="B3B1_B1T">Especie</p>
                        <p class="showMV">Robusta</p>
                        <p class="B3B1_B1T">Cafeína</p>
                        <p class="showMV">Muy alto</p>
                    </div>
                    <div class="WLine"></div>
                    <div class="showDK B3B1_B2">
                        <p>Oscuro</p>
