                        <p>Muy alto</p>
                    </div>
                </div>
                <div class="B3B2">
                    <p>Nuestra selección de café es de granos orgánicos de <span>Chiapas</span>, la tierra del café, de nuestro país un orgullo.</p>
                    <p>Tostamos con leña de mezquite de <span>Sonora</span> (Huupa®) por su alta calidad de poder calorífico, ya que ésta resalta el aroma, cuerpo y sabor del café.</p>
                </div>
